.NavLink {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: inherit;
    padding: 5px;
    padding-left: 4%;
    padding-right: 4%;
    margin: 3%;
    white-space: nowrap;
    border: 1px solid rgba(255, 255, 255, 0.219);
    transition: background-color cubic-bezier(.26,.15,.46,1) 0.35s;
}

.NavLink:hover {
    background: rgba(0, 224, 19, 0.5);
    border: 1px solid white;
}

.NavLink .icon {
    padding-right: 5px;
}